header {
    display: flex;
    align-items: center;
    height: 100px;
    padding: 15px 25px;
    background-color: #000;
    width: 100%;
  }
  .header_container {
    display: flex;
    align-items: center;
    margin: 0 auto;
    max-width: 1140px;
    width: 100%;
  }
  .logo {
    padding: 0 16px;
  }
  .menu {
    width: 70%;
    color: #fff;
    text-decoration: none;
  }
  .menu a {
    display: inline-block;
    color: #fff;
    padding: 5px;
    font-weight: 600;
    text-decoration: none;
  }
  .menu a:hover {
    text-decoration: underline;
  }
  .menu li {
    list-style: none;
  }
  .check_button {
    width: 200px;
    padding: 10px 5px;
    background-color: #4a90e2;
    color: #fff;
    text-align: center;
    font-weight: 700;
    border: 0;
    border-radius: 12px;
    cursor: pointer;
    outline: none;
  }
  .hover_button:hover {
    transition: .9s;
    box-shadow: 0 2px 6px 0 rgba(74,144,226,.5);
  }