.order_container {
    width: 30%;
    max-width: 275px;
    text-align: center;
}
.order {
    position: sticky;
    top: 10px;
    margin-top: 93px;
    border-top: 1px solid #eaeaea;
}
.total_price {
    font-size: 1.5rem;
    font-weight: 700;
    color: #333;
}
.price_container {
    padding: 0 20px;
    margin-top: 30px;
}
.deadline {
    min-height: 10px;
    color: #999;
    font-size: .9rem;
    margin: 10px 0;
}
.calculate_button {
    display: block;
    width: 100%;
    background-color: #4a90e2;
    font-weight: 700;
    border-radius: 12px;
    color: #fff;
    border: 0;
    padding: 10px;
    outline: none;
    cursor: pointer;
    transition: 1s;
}
.calculate_button:disabled {
    cursor: auto;
    background-color: #1b5dab;
    transition: 1s;
}
  